'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function DiagnosisPage() {
  const [symptom, setSymptom] = useState('');
  const [analyzing, setAnalyzing] = useState(false);

  const commonSymptoms = [
    { icon: 'üîä', label: 'Strange Noises', desc: 'Clicking, grinding, squealing' },
    { icon: 'üí®', label: 'Smoke or Smells', desc: 'Unusual exhaust or burning' },
    { icon: '‚ö°', label: 'Warning Lights', desc: 'Dashboard indicators' },
    { icon: 'üîß', label: 'Performance Issues', desc: 'Stalling, rough idle' },
    { icon: 'üíß', label: 'Leaks', desc: 'Fluid spots under car' },
    { icon: 'üéØ', label: 'Vibrations', desc: 'Shaking while driving' },
  ];

  const handleAnalyze = (e: React.FormEvent) => {
    e.preventDefault();
    setAnalyzing(true);
    // Simulate analysis
    setTimeout(() => setAnalyzing(false), 2000);
  };

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      {/* Header */}
      <div className="text-center">
        <div className="text-6xl mb-4">üîç</div>
        <h1 className="text-4xl font-bold gradient-text mb-4">AI Vehicle Diagnosis</h1>
        <p className="text-gray-400 text-lg max-w-2xl mx-auto">
          Describe your vehicle&apos;s symptoms and our AI will analyze potential issues, 
          estimated costs, and recommended actions.
        </p>
      </div>

      {/* Symptom Input */}
      <div className="glass-strong rounded-2xl p-8">
        <form onSubmit={handleAnalyze} className="space-y-6">
          <div>
            <label className="block text-lg font-semibold mb-3">
              What&apos;s happening with your vehicle?
            </label>
            <textarea
              value={symptom}
              onChange={(e) => setSymptom(e.target.value)}
              placeholder="Example: My car makes a grinding noise when I brake, especially at low speeds. The noise seems to come from the front wheels..."
              rows={5}
              className="w-full glass rounded-xl px-6 py-4 bg-transparent border border-gray-700 focus:border-[#2ec8c6] focus:outline-none transition-colors resize-none text-lg"
            />
          </div>

          <button
            type="submit"
            disabled={!symptom.trim() || analyzing}
            className="w-full py-4 bg-[#2ec8c6] hover:bg-[#26a5a3] text-black font-bold text-lg rounded-xl transition-all disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-3"
          >
            {analyzing ? (
              <>
                <div className="w-5 h-5 border-2 border-black border-t-transparent rounded-full animate-spin"></div>
                Analyzing...
              </>
            ) : (
              <>
                <span>üß†</span>
                Analyze with AI
              </>
            )}
          </button>
        </form>
      </div>

      {/* Common Symptoms */}
      <div className="glass-strong rounded-2xl p-8">
        <h2 className="text-xl font-bold mb-6">Common Symptoms</h2>
        <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
          {commonSymptoms.map((item, idx) => (
            <button
              key={idx}
              onClick={() => setSymptom(item.label + ': ' + item.desc)}
              className="glass rounded-xl p-4 text-left hover:bg-white/5 transition-all hover:scale-[1.02]"
            >
              <div className="text-3xl mb-2">{item.icon}</div>
              <div className="font-semibold">{item.label}</div>
              <div className="text-sm text-gray-400">{item.desc}</div>
            </button>
          ))}
        </div>
      </div>

      {/* How it works */}
      <div className="glass-strong rounded-2xl p-8">
        <h2 className="text-xl font-bold mb-6">How AI Diagnosis Works</h2>
        <div className="grid md:grid-cols-3 gap-6">
          <div className="text-center">
            <div className="w-16 h-16 bg-gradient-to-br from-[#2ec8c6]/20 to-[#1a9a99]/20 rounded-full flex items-center justify-center text-3xl mx-auto mb-4">
              1Ô∏è‚É£
            </div>
            <h3 className="font-bold mb-2">Describe Symptoms</h3>
            <p className="text-sm text-gray-400">Tell us what you&apos;re experiencing with your vehicle</p>
          </div>
          <div className="text-center">
            <div className="w-16 h-16 bg-gradient-to-br from-[#2ec8c6]/20 to-[#1a9a99]/20 rounded-full flex items-center justify-center text-3xl mx-auto mb-4">
              2Ô∏è‚É£
            </div>
            <h3 className="font-bold mb-2">AI Analysis</h3>
            <p className="text-sm text-gray-400">Our AI analyzes symptoms against thousands of cases</p>
          </div>
          <div className="text-center">
            <div className="w-16 h-16 bg-gradient-to-br from-[#2ec8c6]/20 to-[#1a9a99]/20 rounded-full flex items-center justify-center text-3xl mx-auto mb-4">
              3Ô∏è‚É£
            </div>
            <h3 className="font-bold mb-2">Get Recommendations</h3>
            <p className="text-sm text-gray-400">Receive diagnosis, cost estimates, and shop recommendations</p>
          </div>
        </div>
      </div>

      {/* CTA */}
      <div className="text-center">
        <p className="text-gray-400 mb-4">Need professional help right away?</p>
        <Link
          href="/dashboard/shops"
          className="inline-flex items-center gap-2 px-6 py-3 glass rounded-lg hover:bg-white/10 transition-colors"
        >
          <span>üîß</span>
          Find Trusted Repair Shops
        </Link>
      </div>
    </div>
  );
}

